{\n    dispaly: none;\n    width: 0px;\n    /* Remove scrollbar space */\n    background: transparent;\n    /* Optional: just make scrollbar invisible */\n}\n\n/* Optional: show position indicator in red */\n::-webkit-scrollbar-thumb {\n    dispaly: none;\n    background: transparent;\n}\n\n$breakpoint: 735px;\n\n@mixin mq {\n    @media screen and (max-width: $breakpoint) {\n        @content;\n    }\n}\n\n// html.iphone {\n//     transform: rotate(-89deg);\n//     transform-origin: left top;\n//     width: 100vh;\n//     overflow-x: hidden;\n//     position: absolute;\n//     top: 100%;\n//     left: 0;\n// }","@import 'base';\n\n#main {\n    position: relative;\n    width: 100vw;\n    height: 100vh;\n    overflow: hidden;\n    display: block;\n}\n\n.gui {\n    color: $col_frnt;\n}\n\n.gui-layer {\n    z-index: $gui-z-index;\n}\n\n#gui-panel {\n    z-index: $gui-z-index*2;\n}\n\n.code-container {\n    position: absolute;\n    top: 0px;\n    left: 0px;\n    display: flex;\n    overflow: hidden;\n\n    width: 100vw;\n\n    transition: all $anm-middle;\n\n    transform: translate(0, 0);\n\n    @include mq {\n        top: $gui-size-unit-sp;\n    }\n}\n\n.code_hide {\n    width: 0vw;\n}\n\n.code-container-mov-l {\n    transform-origin: center top;\n    animation: anm-code-container-mov-l 0.2s both;\n    animation-timing-function: ease-in-out;\n    animation-delay: 0.0s;\n}\n\n.code-container-mov-r {\n    transform-origin: center top;\n    animation: anm-code-container-mov-r 0.2s both;\n    animation-timing-function: ease-in-out;\n    animation-delay: 0.0s;\n}\n\n.code-container-hidden {\n    transform-origin: left top;\n    flex: 0 0 0;\n    width: 0%;\n}\n\n@keyframes anm-code-container-mov-l {\n    0% {\n        opacity: 1;\n        // transform: translate($gui-size-unit*-0.25, 0);\n    }\n\n    100% {\n        opacity: 1;\n        // transform: translate(0, 0);\n    }\n}\n\n@keyframes anm-code-container-mov-r {\n    0% {\n        opacity: 1;\n        // transform: tra