ht: 100%;\n        width: $gui-size-unit*2.5*5+$gui-size-unit*5;\n\n        .ghdr-btn {\n            right: 0px;\n            text-align: center;\n            color: $col-bg-bright-lighten;\n            height: 100%;\n            background: $col-bg;\n            margin-left: 1px;\n\n            &.btn-record {\n                font-size: 18px;\n                color: $col-frnt;\n                width: $gui-size-unit;\n                display: flex;\n                justify-content: center;\n                align-items: center;\n                vertical-align: middle;\n                line-height: 2.0;\n\n                @extend %btn-interact;\n\n                &::after {\n                    transition: all $anm-long;\n                    content: '';\n                    display: block;\n                    width: 10px;\n                    height: 10px;\n                    border-radius: 10px;\n                    background: #FF0000;\n                }\n\n                &:focus::after {\n                    content: '';\n                    display: block;\n                    width: 10px;\n                    height: 10px;\n                    border-radius: 10px;\n                    background: #FF0000;\n                }\n\n                &:hover::after {\n                    content: '';\n                    display: block;\n                    width: 10px;\n                    height: 10px;\n                    border-radius: 10px;\n                    box-shadow: 0px 0px 15px 1px #FF0000;\n                    background: #FF6666;\n                }\n\n                &.active::after {\n                    content: '';\n                    display: block;\n                    width: 10px;\n                    height: 10px;\n                    border-radius: 10px;\n                    box-shadow: 0px 0px 15px 1px #FF0000;\n                    background: #FF6666;\n                    animation: anm-recording 1.5s both;\n                    animation-tim