;\n        }\n    }\n}","@import '../base';\n\n// main\n///////////////////////////////////////////////////\n.tl-root {\n    position: absolute;\n    z-index: $gui-z-index;\n    bottom: 0px;\n    padding: 0px 45px;\n    align-items: center;\n    display: flex;\n    background: $col-bg;\n    width: 100%;\n    height: 45px;\n    transition: all $anm-short;\n\n    @include mq {\n        display: none;\n    }\n}\n\n.tl_hide {\n    bottom: -$gui-size-unit;\n}\n\n#tl-base {\n    width: 100%;\n    height: 5px;\n    z-index: 0;\n    border-radius: 10px;\n    background: $col-bg;\n}\n\n#tl-workspace {\n    position: absolute;\n    top: 20px;\n    height: 5px;\n    z-index: 1;\n    text-align: center;\n    align-items: center;\n    display: flex;\n    justify-content: center;\n    font-weight: 400;\n    background: $col-bg-bright;\n    font-size: 0.5rem;\n    width: 45px;\n    cursor: move;\n    letter-spacing: 1px;\n    line-height: 2;\n}\n\ninput[type=number]::-webkit-inner-spin-button {\n    -webkit-appearance: none;\n}\n\n%num {\n    font-weight: 400;\n    text-align: center;\n    letter-spacing: 1px;\n    line-height: 2;\n    font-size: 0.5rem;\n    color: $col-frnt;\n    background: $col-bg;\n    padding-left: 3px;\n    border: 0px;\n}\n\n#tl-total-frames {\n    position: absolute;\n    @extend %num;\n    padding-left: 2px;\n    top: 25px;\n    right: 0px;\n    width: 45px;\n    height: 20px;\n    z-index: 1;\n    text-align: center;\n    align-items: center;\n    display: flex;\n    justify-content: center;\n\n    &:hover {\n        color: $col-frnt-bright;\n        background: $col-bg-bright;\n    }\n}\n\n#tl-offset-frames {\n    @extend #tl-total-frames;\n    padding-left: 3px;\n    left: 0px;\n}\n\n%range-handle {\n    position: absolute;\n    top: 0px;\n    z-index: 2;\n    text-align: center;\n    align-items: center;\n    display: flex;\n    justify-content: center;\n\n    &:hover {\n        &>.tl-header {\n            color: $col-frnt-bright;\n            background: $col-bg-bright;\n        }\n\n        &>.tl-tip {\n            background: $col-frnt;\n        }\n    }\n\n    .tl-header {\n        @extend %num;\n        width: 45px;\n        height: 20px;\n        cursor: move;\n        z-index: 3;\n        background: #00000000;\n        transition: all $anm-middle;\n\n        &::selection {\n            background: transparent;\n        }\n\n        &::-moz-selection {\n            background: transparent;\n        }\n    }\n\n    .tl-tip {\n        position: absolute;\n        top: 15px;\n        width: 9px;\n        height: 9px;\n        border-radius: 10px;\n        z-index: 4;\n        background: $col-bg-bright; // -webkit-transform: skew(0deg, 45deg);\n        // transform: skew(0deg, 45deg);\n        transition: all $anm-middle;\n    }\n}\n\n::selection {\n    background: transparent;\n}\n\n::-moz-selection {\n    background: transparent;\n}\n\n#tl-start {\n    @extend %range-handle;\n\n    .tl-tip {\n        top: 18px;\n        right: -5px; // -webkit-transform: skew(0deg, 45deg);\n        // transform: skew(0deg, 45deg);\n    }\n}\n\n#tl-end {\n    @extend %range-handle;\n\n    .tl-tip {\n        top: 18px;\n        left: -5px; // -webkit-transform: skew(0deg, -45deg);\n        // transform: skew(0deg, -45deg);\n    }\n}\n\n#tl-current {\n    @extend %range-handle;\n    top: 25px;\n\n    .tl-tip {\n        top: -7px;\n        right: -5px;\n        width: 9px;\n        height: 9px;\n        background: $col-frnt; // -webkit-transform: skew(0deg, 0deg);\n        // transform: skew(0deg, 0deg);\n    }\n}"],"sourceRoot":""}]);

